


























import {
    Component, Vue, Prop, Emit,
} from 'vue-property-decorator'
import UiButton from '../UiButton.vue'

@Component({
    components: { UiButton },
})
export default class SimpleModal extends Vue {
    @Prop() title: string
    @Prop() content: string
    @Prop() no: string
    @Prop() yes: string
    @Prop() yesType: string

    hideModal() {
        this.$emit('close')
    }
}
